/* Styles go in this file. */

html, body { height: 100%; overflow: hidden; }

body {
	margin: 0;
	font-size: 12px;
	-webkit-font-smoothing: antialiased;
}

* {
	box-sizing: border-box;
}

svg {
	display: block;
}
#viz-ui {
	position: relative;
	width: 100%;
	.fl-controls-container {
		margin-right: 1em;
		vertical-align: top;
	}
	#horse-controls, #filter-control {
		display: inline-block;
		vertical-align: top;
	}
	#replay {
		cursor: pointer;
	}
}


.x.axis line,
.x.axis path {
	display: none;
}

.x.axis text, .y.axis text {
	font-size: 11px;
}

.horse, .rank-number, .end.circle, .name-bg, .name-fg, image, .check line, .check circle {
	transition: 50ms opacity linear;
}

.rank-number {
	font-weight: bold;
}

.tick line {
	opacity: 1;
}

#caption {
	display: flex;
	position: absolute;
	box-shadow: 0 0 8px rgba(0,0,0,0.25);
	opacity: 0;
	overflow: hidden;
	max-height: 70vh;
	pointer-events: none;
	transition: opacity 0.66s ease;
	max-width: 90%;
	#image {
		line-height: 0;
		text-align: center;
		img {
			width: 100%;
			height: auto;
			max-height: 40vh;
			max-width: 100%;
		}
	}
}

.fl-label {
	user-select: none;
}

@import "customer";